import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
    user: {
        rank: '',
        uid: '',
        username: '',
        uuid: ''
    }
}

const mutations = {
    UPDATE_USER (state, userinfo) {
        state.user = userinfo
    }
}

const store = new Vuex.Store({
    state,
    mutations
})

export default store



