<template>
    <div class="wrap" :class="{ showmenu: showmenu }" v-if="loding">
        <div class="header">
            <a @click="show" href="javascript:void(0)"><i class="fa fa-bars" alt="菜单" title="菜单"></i></a>
            <a v-link="{ name: 'DAIndex' }" href="javascript:void(0)"><i class="fa fa-home"></i></a>
            <p class="path noSelect">深大百科控制台 <i class="fa fa-angle-right"></i> {{ pagepath }}</p>

            <div class="bugrep noSelect" alt="bug报告" title="bug报告" v-if="!hide">
                <p><i class="fa fa-bug"></i></p>
            </div>
            <div class="user noSelect" v-if="!hide">
                <p>{{ user.username }} <i class="fa fa-angle-down"></i></p>
                <ul>
                    <li v-link="{ name: 'DASetting' }">账户设置</li>
                    <li @click="logout">退出账户</li>
                </ul>
            </div>
            <div class="mymessage noSelect" v-if="!hide">
                <p>我的消息 <i class="fa fa-angle-down"></i></p>
                <ul>
                    <li class="empty"><i class="fa fa-archive"> 暂无消息</i></li>
                </ul>
            </div>
        </div>
        <router-view></router-view>
    </div>
    <div class="menuContainer" v-if="loding">
        <div class="menu">
            <div class="menuheader" v-link="'/dashboard'" @click="show">
                <h1>深大百科</h1>
                <p>Dashboard</p>
            </div>
            <div class="list">
                <ul>
                    <li v-link="{ name: 'DAAffair' }" @click="show"><i class="fa fa-envelope-o"></i> 事务处理</li>
                    <li v-link="{ name: 'DAActivity' }" @click="show" v-if="user.rank >= 5"><i class="fa fa-cutlery"></i> 活动管理</li>
                    <li v-link="{ name: 'DACard' }" @click="show"><i class="fa fa-credit-card"></i> 校园卡找回</li>
                    <li v-link="{ name: 'DASetting' }" @click="show"><i class="fa fa-cogs"></i> 设置</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import store from '../vuex/store'

export default {
    data () {
        return {
            showmenu: false,
            pagepath: '',
            hide: false,
            loding: false,
            user: {}
        }
    },
    store,
    methods: {
        show: function () {
            this.showmenu = !this.showmenu
        },
        loadUser () {
            if (window.location.hostname === 'localhost') {
                let user = {
                    rank: '5',
                    uid: '1',
                    username: '2014130355',
                    uuid: '112345DAS'
                }
                this.user = user
                var d = new Date()
                d.setHours(d.getHours() + 1)
                document.cookie = 'uuid=' + this.user.uuid + '; expires=' + d.toGMTString()
                store.dispatch('UPDATE_USER', this.user)
            } else {
                this.$http.get('http://szdx.sinaapp.com/dashboard/rest/v2/login/user.php').then(res => {
                    if (res.data === 'login timeout') {
                        this.$router.go('/')
                    } else {
                        let user = res.data
                        this.user = user
                        store.dispatch('UPDATE_USER', user)
                    }
                })
            }
        },
        logout () {
            let userEmpty = {
                rank: '',
                uid: '',
                username: '',
                uuid: ''
            }
            store.dispatch('UPDATE_USER', userEmpty)
            document.cookie = ''
            this.$router.go('/')
        }
    },
    watch: {
        '$route.namezh': function () {
            this.pagepath = this.$route.namezh
        }
    },
    ready () {
        let cookie = document.cookie.split(';')
        let cookies = {}
        cookie.map(function (item) {
            let items = item.split('=')
            items[0].replace(/\s+/, '')
            cookies[items[0]] = items[1]
        })
        if (!cookies.uuid) {
            if (window.location.hostname !== 'localhost') {
                this.$router.go('/')
                return
            }
        }
        this.loadUser()
        setInterval(() => {
            this.loadUser()
        }, 1000 * 30)
        this.loding = true
        this.pagepath = this.$route.namezh
    }
}
</script>

<style>
body {
    min-width: 1000px;
}
</style>

<style lang='less' scoped>
.menuContainer {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    min-width: 1000px;
    height: 100%;
    background-image: url(../assets/dashboard_bg.jpg);
    background-size: 100% 100%;
}
.menu {
    position: absolute;
    left: 0;
    top: 0;
    width: 200px;
    height: 100%;
    z-index: 75;
    .menuheader {
        height: 100px;
        color: #FFF;
        padding: 15px;
        text-shadow: 0 2px 3px rgba(0, 0, 0, .3);
        text-align: center;
        cursor: pointer;
        h1 {
            font-size: 24px;
            line-height: 40px;
        }
        p {
            font-size: 16px;
            line-height: 30px;
        }
    }
    .list {
        color: #FFF;
        li {
            margin-top: 10px;
            padding: 10px 25px;
            list-style: none;
            font-size: 18px;
            line-height: 30px;
            cursor: pointer;
            i {
                width: 30px;
            }
        }
        .v-link-active {
            border-left: 2px solid rgba(255, 255, 255, .9);
            background: rgba(0, 0, 0, .3);
        }
    }
}

.wrap {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    min-width: 1000px;
    height: 100%;
    background: #FFF;
    transition: all .3s ease;
    transform: translate(0, 0);
    overflow: hidden;
    z-index: 100;
    .header {
        width: 100%;
        height: 60px;
        background: #F6F6F6;
        border-bottom: 1px solid #ddd;
        a {
            float: left;
            color: #444;
            padding: 0 20px;
            font-size: 20px;
            line-height: 60px;
            &:hover {
                background: #ddd;
            }
            &:active {
                background: #ddd;
            }
        }
        p {
            color: #444;
            padding: 0 10px;
            font-size: 16px;
            font-weight: bold;
            line-height: 60px;
        }
        .mymessage {
            position: absolute;
            top: 0;
            right: 220px;
            width: 100px;
            padding: 0;
            cursor: pointer;
            text-align: left;

            .unwatch {
                width: 24px;
                color: #FFF;
                font-size: 12px;
                text-align: center;
                line-height: 24px;
                background: #F45757;
                border-radius: 50%;
                vertical-align: 2px;
                display: inline-block;
            }

            ul {
                position: absolute;
                top: 59px;
                left: -70px;
                width: 320px;
                padding: 10px 0;
                color: #444;
                background: #FFF;
                display: none;
                z-index: 1000;
                box-shadow: 0 3px 2px rgba(0, 0, 0, .2);
                border-radius: 0 0 2px 2px;

                li {
                    list-style: none;
                    font-size: 14px;
                    line-height: 40px;
                    padding: 0 10px;
                    border-bottom: 1px solid rgba(0, 0, 0, .03);
                    cursor: pointer;
                    display: block;

                    &:hover {
                        background: rgba(0, 0, 0, .05);
                    }
                }

                li:last-child {
                    border-bottom: none;
                }

                li.empty {
                    font-size: 16px;
                    text-align: center;

                    &:hover {
                        background: none;
                    }
                }
            }

            &:hover {
                background: #FFF;
                border-radius: 0 0 2px 2px;
                box-shadow: 0 3px 2px rgba(0, 0, 0, .2);
            }

            &:hover ul {
                display: block;
            }
        }
        .bugrep {
            position: absolute;
            top: 0;
            right: 0;
            width: 60px;
            font-size: 14px;
            text-align: center;
            z-index: 1000;

            &:hover {
                background: rgba(0, 0, 0, .05);
            }

            &:active {
                background: rgba(0, 0, 0, .1);
            }
        }
        .user {
            position: absolute;
            top: 0;
            right: 60px;
            width: 160px;
            text-align: center;
            z-index: 1000;

            ul {
                position: relative;
                top: -1px;
                left: 0;
                width: 100%;
                color: #444;
                background: #FFF;
                display: none;
                z-index: 1000;

                li {
                    list-style: none;
                    font-size: 14px;
                    line-height: 40px;
                    margin-bottom: 5px;
                    cursor: pointer;

                    &:hover {
                        background: rgba(0, 0, 0, .05);
                    }
                }
            }

            &:hover {
                background: #FFF;
                border-radius: 0 0 2px 2px;
                box-shadow: 0 3px 2px rgba(0, 0, 0, .2);
            }

            &:hover ul {
                display: block;
            }
        }
    }
}

.showmenu {
    transform: translateX(200px);
    box-shadow: 0 3px 5px rgba(0, 0, 0, .2);
}
</style>



